import React from "react"
import './step-inpu.css'
import { InputNumber } from 'rsuite';

export default ({InputMax, InputValue, BoxId}) => {
    return (
        <div>
            <InputNumber
                defaultValue={InputValue}
                max={InputMax}
                min={0}
                //onChange={this.handleChange}
                onChange={(newValue) => {
                    console.log('onChange:', newValue);
                    if ( newValue == 0 ) {
                        document.getElementById("product-variable-" + BoxId).classList.remove("active");
                    } else {
                        document.getElementById("product-variable-" + BoxId).classList.add("active");
                    }

                }}
            />
        </div>
    );
};
